import React, { FC } from "react";
import { ThemeEvents, useWhiteTheme } from "providers";
import { decorationList } from "./components";

export const EventDecoration: FC<{
  event: ThemeEvents;
}> = ({ event }) => {
  if (!event) {
    return null;
  }

  const Component = decorationList[event];

  return <Component />;
};
